:root {
  font-family: Inter, Avenir, Roboto, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;

  color:#242424;
  background-color:  rgba(255, 255, 255, 0.97); 


  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;


  -webkit-text-size-adjust: 100%;

 
  --font_family: YouTube Sans,Roboto, Inter, Avenir,  Helvetica, Arial, sans-serif;
}

a {
  font-weight: 500;
  color: #646cff;
  text-decoration: inherit;
}
a:hover {
  color: #535bf2;
}

body {
  margin: 0;
 
  place-items: center;
  min-width: 320px;
  min-height: 100vh;
}

h1 {
  font-size: 3.2em;
  line-height: 1.1;
}

button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  color: #1a1a1a;
  background-color: #f1f1f1;
  cursor: pointer;
  transition: border-color 0.25s;
}
button:hover {
  border-color: #646cff;
}
button:focus,
button:focus-visible {
  /*outline: 4px auto -webkit-focus-ring-color;*/
  border-color: #846cff;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
  a:hover {
    color: #747bff;
  }
  button {
    background-color: #f9f9f9;
  }
}





.flexSpace {
	-ms-flex: 1 1 0.000000001px;
	flex: 1 1;
	-ms-flex-preferred-size: 0.000000001px;
	flex-basis: 0.000000001px;
}


.FlexList {
	box-align: start;
	-ms-flex-align: start;
	align-items: flex-start;
	display: flex;
	flex-wrap: wrap;
}

.FlexItem {
	box-flex: 1;
	-ms-flex-positive: 1;
	flex-grow: 1;
	display: inline-block;
	text-align: center;
}


._dsplFlx {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
}

.spaceAround {
  -ms-flex-pack: space-around;
  -webkit-justify-content: space-around;
  justify-content: space-around;
}





