.date2Upd{
  color:  var(--hrm_palette-blue-purple);
  font-weight:600; 
  cursor: pointer;
}



.total_comprobante_title {
  margin-right: 12px;
  line-height: 42px;
  color: var(--hrm_palette-inverse-background-b);
}

.total_comprobante {
  padding: 6px;
  background-color:  var(--hrm_palette-blue-purple);
  border-radius: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f1f1f1;
  font-weight: 600;
  width: 30px;
  height: 30px;
  text-align: center;
}



.button-shape {
  display: flex;
  flex: 1;
  flex-basis: 1e-9px;
}



.button-shape-next {
  position: relative;
  margin: 0;
  white-space: nowrap;
  min-width: 0;
  text-transform: none;
  font-family: "Roboto","Arial",sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  border: none;
  cursor: pointer;
  outline-width: 0;
  box-sizing: border-box;
  background: none;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
  flex: 1;
  flex-basis: 1e-9px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}


.button-shape-next--tonal {
  color: var(--hrm_palette-text-primary);
  background-color: var(--hrm_palette-badge-chip-background);
}

.button-shape-next--tonal:hover {
  background-color: var(--hrm_palette-mono-tonal-hover);
  border-color: transparent;
}


.total_comprobante.gray {
  color: var(--hrm_palette-text-primary);
  background-color: var(--hrm_palette-badge-chip-background);
}



